import React, { Component } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import "./styles/Product.css"
import * as firebase from "firebase"

class Product extends Component {
    async sumar(nombre, cantidad) {
        const nuevaCantidad = parseInt(prompt("Ingresar cantidad a sumar"))
        await this.sumarArticulo(nombre, cantidad, nuevaCantidad, (this.props.seccion + "2"))
        if (nombre === "Buje 3 Pulg" || nombre === "Buje 4 Pulg" || (nombre === "Valvula De Pie" || nombre === "Valvula Bajo Surtidor" || nombre === "Valvula Bajo Surtidor 2 Pulg" || nombre === "Tapa 3 Pulg")) {
            if (this.props.seccion === "productos") {
                if (nombre === "Buje 3 Pulg" || nombre === "Buje 4 Pulg") {
                    await this.descontarArticulo(nombre, this.props.noMecanizados[nombre].cantidad, nuevaCantidad, "no-mecanizados2")
                }
                else {
                    if (nombre === "Tapa 3 Pulg") {
                        await this.descontarArticulo(nombre, this.props.mecanizados[nombre].cantidad, nuevaCantidad, "mecanizados2")
                        await this.descontarArticulo("Gatillos", this.props.mecanizados["Gatillos"].cantidad, nuevaCantidad, "mecanizados2")
                    }
                    else {
                        await this.descontarArticulo(nombre, this.props.mecanizados[nombre].cantidad, nuevaCantidad, "mecanizados2")
                    }
                }
            }
            else if (this.props.seccion === "mecanizados") {
                try {
                    await this.descontarArticulo(nombre, this.props.noMecanizados[nombre].cantidad, nuevaCantidad, "no-mecanizados2")
                }
                catch{
                }
            }
        }
    }
    async sumarArticulo(nombre, cantidadActual, nuevaCantidad, seccion) {
        await firebase.database().ref().child(seccion).child(nombre).update({
            cantidad: (cantidadActual + nuevaCantidad)
        })
    }
    async descontarArticulo(nombre, cantidadActual, nuevaCantidad, seccion) {
        console.log(cantidadActual)
        await firebase.database().ref().child(seccion).child(nombre).update({
            cantidad: (cantidadActual - nuevaCantidad)
        })
    }
    render() {
        return (
            <div className="product bg-dark">
                <img src={this.props.imagen} className="productImg" alt="" />
                <div className="product-Info">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h5 className="product-name">{this.props.nombre}</h5>
                            </div>
                            <div className="col-6 text-center">
                                <h4><span className="badge badge-primary">{this.props.cantidad}</span></h4>
                            </div>
                            <div className="col-6 text-center">
                                <h4>
                                    <FontAwesomeIcon icon={faPlus} className="icon" onClick={async () => {
                                        await this.sumar(
                                            this.props.nombre,
                                            this.props.cantidad
                                        )
                                    }} />
                                </h4>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Product