import React, { Component } from "react"
import NavBar from "../components/NavBar"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import Deuda from "../components/Deuda"
import * as firebase from "firebase"

class Deudas extends Component {
    state = {
        user: undefined,
        loading: true,
        error: null,
        deudas: []
    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async fetchData() {
        const productosRef = await firebase.database().ref().child('clientes2')
        productosRef.on("value", snapshot => {
            const arr = []
            Object.keys(snapshot.val()).map(cliente => {
                if (snapshot.val()[cliente].datos.deuda !== 0) {
                    arr.push([
                        cliente,
                        snapshot.val()[cliente].datos.deuda
                    ]
                    )
                }
            })
            this.setState(
                {
                    deudas: arr,
                    loading: false
                }
            )
        })
    }
    authListener() {
        firebase.auth().onAuthStateChanged((user) => {
            console.log(user)
            if (user) {
                this.setState({ user })
                console.log("user added")
            }
            else {
                this.setState({
                    user: null
                })
                console.log("user quit")
            }
        })
    }
    componentDidMount() {
        this.authListener()
        this.fetchData()
    }
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayDeudas = document.getElementsByClassName("col-12 col-md-6 col-lg-4 form-group text-center")
        forEach.call(arrayDeudas, function (f) {
            if (f.children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-12 col-md-6 col-lg-4 form-group text-center d-none";
            }
            else {
                f.className = "col-12 col-md-6 col-lg-4 form-group text-center d-flex";
            }
        });
    }
    async updateDeuda(cliente, nuevaDeuda) {
        const datosRef = await firebase.database().ref().child('clientes2').child(cliente).child("datos")
        datosRef.update({
            deuda: nuevaDeuda
        })
    }
    logOut() {
        firebase.auth().signOut()
    }
    render() {
        try {
            const user = this.state.user.email
            if (user) {
                if (this.state.loading) {
                    return (
                        <div>
                            <NavBar handleClick={this.logOut} />
                            <SpinnerLoading />
                        </div>
                    )
                }
                else {
                    if (this.state.deudas.length) {
                        return (
                            <div>
                                <NavBar handleClick={this.logOut} />
                                <div className="productList">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-6 offset-3">
                                                <form>
                                                    <div className="formRow">
                                                        <div className="form-group col-12 text-center">
                                                            <input type="text" className="form-control" onChange={
                                                                e => {
                                                                    this.handleChange(e)
                                                                }
                                                            } placeholder="Buscar Deuda" />
                                                        </div>
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                        <div className="row">
                                            {this.state.deudas.map(cliente => (
                                                <div className="col-12 col-md-6 col-lg-4 form-group text-center">
                                                    <Deuda cliente={cliente[0]} deuda={cliente[1]} handleClick={this.updateDeuda} />
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                    else {
                        return (
                            <div>
                                <NavBar handleClick={this.logOut} />
                                <div className="productList">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-12 text-center">
                                                <h3>No Hay Deudas Pendientes!</h3>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                }
            }
        }
        catch{
            return (
                <Login redirect="Deudas" />
            )
        }
    }
}
export default Deudas
