import React, { Component, Fragment } from "react"
import Item from "./Item"
import "./styles/ficha.css"
class Ficha extends Component {
    render() {
        let info = Object.keys(this.props.data)
        return (
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h1>{this.props.data.nombre}</h1>
                    </div>
                    {info.map(item => (
                        (item === "nombre" ?
                            null
                            :
                            <div className="col-12 col-md-6 form-group text-center d-flex align-items-center">
                                <Item seccion={this.props.seccion} cliente={this.props.data.nombre} name={item} data={this.props.data[item]} />
                            </div>

                        )))}
                </div>
            </div>
        )
    }
}
export default Ficha

