import React, { Component, Fragment } from "react"
import "./styles/ficha.css"
import * as firebase from "firebase"

class Item extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    componentDidMount() {
    }
    async modificarDeuda() {
        const nuevaDeuda = parseInt(prompt("Ingresar Nueva Deuda"))
        const deudaRef = await firebase.database().ref().child(this.props.seccion + "2").child(this.props.cliente).child("datos")
        await deudaRef.update({
            deuda: nuevaDeuda
        }
        )

    }
    render() {
        if (this.props.name === "nombre") {
            return (null)
        }
        else {
            if (this.props.name === "telefono" || this.props.name === "direccion") {
                {
                    return (
                        <div className="container color-white bg-dark p-2">
                            <div className="row">
                                <div class="col-12">
                                    <h3>{this.props.name}</h3>
                                </div>
                            </div>
                            <div className="row">
                                {Object.keys(this.props.data).map(key => {
                                    return (
                                        <div className="col-12 text-center">
                                            <h3>{this.props.data[key]}</h3>
                                        </div>
                                    )
                                })}
                            </div>
                        </div>
                    )
                }
            }
            return (
                <div className="container ficha bg-dark p-2">
                    <h3>{this.props.name}</h3>
                    {this.props.name === "deuda" ?
                        (this.props.data === 0 ?
                            <h3 className="itemDeuda" onClick={
                                e => {
                                    this.modificarDeuda()
                                }
                            }>{this.props.data}</h3>
                            :
                            <h3>{this.props.data}</h3>)
                        :
                        <h3>{this.props.data}</h3>
                    }
                </div>
            )
        }
    }
}
export default Item