import React, { Component } from "react"
import { Link, Redirect } from "react-router-dom"
import NavBar from "./NavBar"
import Ficha from "./Ficha"
import FichaPedidos from "./FichaPedidos"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faTrash, faEdit } from '@fortawesome/free-solid-svg-icons'
import * as firebase from "firebase"
import "./styles/ficha.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
class FichaProovedor extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async seLLevo() {
        this.agregarArticulo(prompt("Que se llev?"), "Llevo")
        console.log(this.props.proovedor.datos.nombre)
        const pedidosRef = await firebase.database().ref().child('proovedores2').child(this.props.proovedor.datos.nombre).child("pedidos")
        const date = new Date()
        pedidosRef.push({
            fecha: `${date.getDay()}/${date.getMonth()}/${date.getFullYear()}`,
            articulos: this.state.pedido
        })
    }
    agregarArticulo(articulo, accion) {
        const arr = []
        arr.push({
            nombre: articulo,
            cantidad: accion
        })
        this.setState({
            pedido: arr
        })
    }
    async trajo() {
        this.agregarArticulo(prompt("Que Trajo?"), "Trajo")
        console.log(this.props.proovedor.datos.nombre)
        const pedidosRef = await firebase.database().ref().child('proovedores2').child(this.props.proovedor.datos.nombre).child("pedidos")
        const date = new Date()
        pedidosRef.push({
            fecha: `${date.getDay()}/${date.getMonth()}/${date.getFullYear()}`,
            articulos: this.state.pedido
        })
    }

    eliminarCliente = () => {
        confirmAlert({
            title: 'Eliminar Cliente?',
            buttons: [
                {
                    label: 'Eliminar',
                    onClick: async () => {
                        console.log(this.props.seccion)
                        await firebase.database().ref().child(this.props.seccion + "2").child(this.props.proovedor.datos.nombre).remove().then(
                            this.props.handleClick()
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    };
    render() {
        return (
            <div>
                <NavBar />
                <div className="productList">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-5 offset-1 text-left">
                                <h1> <FontAwesomeIcon className="backIcon" icon={faArrowLeft} onClick={this.props.handleClick} />
                                </h1>
                            </div>
                            <div className="col-3 offset-3 danger-zone">
                                <h1> <FontAwesomeIcon className="backIcon" icon={faEdit} /></h1>
                                <h1> <FontAwesomeIcon className="backIcon" icon={faTrash} onClick={e => {
                                    this.eliminarCliente()
                                }} /></h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center form-group">
                                <Ficha seccion={this.props.seccion} data={this.props.proovedor.datos} />
                            </div>
                        </div>
                        {this.props.seccion === "proovedores" ?
                            <div className="row">
                                <div className="col-6 text-center">
                                    <button type="button" className="btn btn-outline-success" onClick={e => {
                                        this.seLLevo()
                                    }}>Se llevo...</button>
                                </div>
                                <div className="col-6 text-center">
                                    <button type="button" className="btn btn-outline-success" onClick={e => {
                                        this.trajo()
                                    }}>Trajo...</button>
                                </div>
                            </div>
                            :
                            null
                        }
                        <div className="row">
                            <div className="col-12 text-center">
                                <h2>Lista de Pedidos</h2>
                            </div>
                            <div className="col-12 text-center form-group">
                                <FichaPedidos data={this.props.proovedor.pedidos} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default FichaProovedor